import {
    refreshList
} from "./mvc/view";

import json from '../assets/notes.json';
import './controllers/controller';

const localNotes = localStorage.getItem('notes');

if (localNotes === null) {
    localStorage.setItem('notes', JSON.stringify(json))
    refreshList();
} else {
    refreshList();
}